import React, { useEffect } from "react";

import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import "./App.scss";

// Pages
import Homepage from "./Components/Pages/homepage";
import SignInUp from "./Components/Pages/Sign-in/signIn.page";
import ForgetPassword from "./Components/Pages/Sign-in/reset-password.page";
import ProductsPage from "./Components/Pages/Products/Products.page";
import Cart from "./Components/Pages/Cart/cart.page";

// Components
import Header from "./Components/Templates/Header/header.component";
import Message from "./Components/Templates/Message/message.component";

import Products from "./Resources/data";

import PrivateRoute from "./Components/PrivatePublicCompenent/PrivateRoute";
import PublicRoute from "./Components/PrivatePublicCompenent/PublicRoute";

// Firebase
import { onStateChanged } from "./Firebase/auth";
import { setUserDB, getUserdataOnSnapashotDB } from "./Firebase/db";

// Redux
import { useDispatch, useSelector } from "react-redux";
import { addUser, selectUser } from "./Redux/userSlice";

const App = () => {
  // NOTE: IMPLEMENTATION OF CONDITIONAL DISPLAY OF CART ICON IS YET TO BE DONE

  const dispatch = useDispatch();

  useEffect(() => {
    onStateChanged(function (callReturn) {
      if (callReturn) {
        const { email } = callReturn;
        setUserDB(email, {
          email: email,
          products: {
            total: 0,
            cart: {},
          },
        });
        dispatch(
          addUser({ authenticated: true, user: JSON.stringify(callReturn) })
        );
      } else dispatch(addUser({ authenticated: false, user: null }));
    });
  });

  const userState = useSelector(selectUser);
  const user = JSON.parse(userState.user);

  // if (user)
  //   getUserdataOnSnapashotDB(
  //     "jashangill3592@gmail.com",
  //     function (res) {
  //       console.log(res);
  //     },
  //     true
  //   );

  const { hats, men, women, shoes } = Products.products;
  return (
    <BrowserRouter>
      <div className="App">
        <Header authenticated={userState.authenticated} />

        <Message />

        <Switch>
          <Route path="/" exact>
            <Homepage />
          </Route>
          <Route path="/hats" exact>
            <ProductsPage category="Hats" products={hats} />;
          </Route>
          <Route path="/shoes" exact>
            <ProductsPage category="shoes" products={shoes} />;
          </Route>
          <Route path="/men" exact>
            <ProductsPage category="Men" products={men} />;
          </Route>
          <Route path="/women" exact>
            <ProductsPage category="Women" products={women} />;
          </Route>

          {/* Different Links of Menu */}
          <Route path="/shop" exact>
            <ProductsPage category="Hats" products={hats} max={4} />;
            <ProductsPage category="shoes" products={shoes} max={4} />;
            <ProductsPage category="Women" products={women} max={4} />;
            <ProductsPage category="Men" products={men} max={4} />;
          </Route>

          <PrivateRoute
            path="/sign-out"
            exact
            authenticated={userState.authenticated}
            component={Homepage}
          ></PrivateRoute>

          <PublicRoute
            path="/sign-in"
            exact
            authenticated={userState.authenticated}
            component={SignInUp}
          ></PublicRoute>

          <PublicRoute
            path="/forget-password"
            exact
            authenticated={userState.authenticated}
            component={ForgetPassword}
          ></PublicRoute>

          <Route exact path="/cart">
            <Cart />
          </Route>

          <Redirect to="/" />
        </Switch>
      </div>
    </BrowserRouter>
  );
};

export default App;
